#menu-button{
  color: #fff;
}

#page-container {
  position: relative;
  min-height: 100vh;
}

.image-preview{
  height: 100px;
  width: 100px;
}

.banner{
  background-image:url('./images/banner-image.jpeg');
  width: 100%;
  height: 300px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.logo{
  height:200px;
  width: 500px;
  margin-top: 25px;
  margin-left: 20px;
}


.site-navbar{
  padding-top: 15px;
  padding-bottom: 15px;
}


@media (min-width: 769px) {
  footer{
    background-color: black;
    height: 5rem;
    position: absolute;
    left: 0;
     bottom: 0;
     width: 100%;

  }

  #content-wrap {
    padding-bottom: 5rem;
  }

  .footer-body{
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }

  .footer-name{
    color: white;
    font-size: 24px;
  }

  .footer-hours{
    color: white;
    font-size: 18px;
  }

  .fa-facebook{
    font-size: 32px;
  }

  .login-button{
    font-size: 18px;
    color: gray;
  }

  .nav-phone{
    display: none;
  }
}

.modal-btn-1{
  margin-right: 30px;
}

@media (max-width: 768px) {
  footer{
    background-color: black;
    height: 4rem;
    position: absolute;
    left: 0;
     bottom: 0;
     width: 100%;

  }

  #content-wrap {
    padding-bottom: 4rem;
  }

  .footer-body{
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }

  .footer-name{
    color: white;
    font-size: 16px;
  }

  .footer-hours{
    color: white;
    font-size: 12px;
  }

  .fa-facebook{
    font-size: 22px;
  }

  .login-button{
    font-size: 14px;
    color: gray;
  }
}

@media (max-width: 439px) {
  .footer-hours{
    display: none;
  }
}

